@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Pappins", sans-serif;
}

body {
  width: 100%;
  min-height: 120vh;
  background: #000;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url("./assets/images/bg01.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

i {
  cursor: pointer;
  user-select: none;
}

ul {
  list-style: none;
}

.container {
  width: 360px;
  border-radius: 15px;
  position: relative;
}

.shape {
  width: 150px;
  height: 150px;
  position: absolute;
  border-radius: 50%;
}

.shape-1 {
  background: radial-gradient(circle at 35px 65px, #9b22ea, #005);
  left: -65px;
  top: -65px;
}

.shape-2 {
  background: radial-gradient(circle at 45px 90px, #ea22b1, #111);
  right: -65px;
  bottom: -65px;
}

.shape-3 {
  width: 60px;
  height: 60px;
  background: #08e2ff;
  right: 7px;
  top: 10px;
  filter: blur(20px);
}

main {
  width: 360px;
  border-radius: 15px;
  border: 10px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 20px rgba(7, 15, 15, 0.5);
  overflow: hidden;
  position: relative;
  backdrop-filter: blur(3px);
}

input[type="range"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  background: #e5e5e5;
  border-radius: 50px;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  -moz-appearance: none;
  background: #4286f4;
  border-radius: 50%;
  cursor: grab;
  width: 10px;
  height: 10px;
}
